@media screen and (max-width: 899px) {
  .slider-item {
    .slider-item-info {
      border-radius: 20px !important;
    }
  }
}

.slider-item {
  width: 100%;
  height: 280px;
  min-height: 280px;
  border-radius: 20px;
  border: 1px solid white;
  transition: all ease-in-out 200ms;

  .slider-item-info {
    height: 280px;
    background-color: rgba($color: #000000, $alpha: 0.7);

    h3 {
      font-size: 26px;
    }
    p {
      margin: 0;
    }

    .item-actions {
      display: flex;
      margin-top: 20px;
    }
    .read-more,
    .read-more-go {
      border: none;
      border-radius: 20px;
      padding: 10px 20px;
      background-color: white;
    }

    .read-more {
      margin-right: 5px;
    }
  }
}
