#projects-component {
  h2 {
    @media screen and (max-width: 899px) {
      font-size: 8vw;
    }
    @media screen and (min-width: 899px) {
      font-size: 4vw;
    }

    text-align: left;
    padding-right: 4vw;
    margin-top: 8vw;
    margin-bottom: 4vw;
  }

  .c-project-container {
    margin-bottom: 4vw;
    transition: all ease-in-out 200ms;
    h3 {
      @media screen and (max-width: 799px) {
        font-size: 3vw;
      }
      @media screen and (max-width: 699px) {
        font-size: 4vw;
      }
      font-size: 2vw;
      margin: 0;
      transition: all ease-in-out 200ms;
    }
    .project-desc {
      margin-bottom: 3vw;
      transition: all ease-in-out 200ms;
    }
    .action {
      margin-top: 2vw;
      transition: all ease-in-out 200ms;
      a {
        display: flex;
        width: 50%;
        border: none;
        color: black;
        text-decoration: none;
        border-radius: 20px;
        padding: 10px 20px;
        align-items: center;
        justify-content: center;
        background-color: white;
        transition: all ease-in-out 200ms;
        span {
          color: black;
          margin-right: 10px;
          font-weight: 400;
          font-size: 16px;
          transition: all ease-in-out 200ms;
        }
      }
      a:hover {
        background-color: #1e6fd9;
        span {
          color: white;
        }
      }
    }
    .project-img {
      @media screen and (max-width: 599px) {
        margin-bottom: 6vw;
      }
      width: 100%;
      height: 30vw;
      border-radius: 20px;
      transition: all ease-in-out 200ms;
    }
  }
}
