footer {
  width: 100%;
  margin-top: 15vw;
  margin-bottom: 8vw;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;

  .wrapper {
    width: 90%;
    margin: auto;
  }

  .grid-name {
    @media screen and (max-width: 599px) {
      display: none;
    }
    h4 {
      font-size: 7vw;
      margin: 0;
    }
    p {
      font-size: 2vw;
    }
  }
  .info-card {
    width: 100%;
    height: 200px;
    color: white;
    border-radius: 20px;
    border: 1px solid white;

    .nav-links {
      display: flex;
      justify-content: center;
      button {
        margin: 4px;
        margin-top: 20px;
        padding: 4px;
        color: white;
        border: none;
        text-decoration: none;
        background-color: transparent;
      }
    }

    .last-words {
      width: 80%;
      margin: auto;
      margin-top: 25px;
      span {
        display: block;
      }
    }
  }
  #landing-contact-info {
    justify-content: center;
    a.contact-info-link {
      display: flex;
      max-width: 30vw;
      align-items: center;
      align-content: center;
      justify-content: center;
      color: #fff;
      text-decoration: none;
      border-radius: 20px;
      border: 1px solid #fff;
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 20px;
      padding-right: 20px;
      img.contact-info-link-icon {
        margin-right: 8px;
      }
    }
  }
}
