@media screen and (max-width: 899px) {
  .left-item,
  .center-item,
  .right-item {
    min-width: 100% !important;
    width: 100% !important;
  }
}

#slider-component {
  align-items: center;
  justify-content: center;
  margin-top: 4vw;
  margin-bottom: 2vw;
  display: flex;
  width: 100%;
  height: 280px;

  .left-item,
  .center-item,
  .right-item {
    min-width: 600px;
    width: 600px;
    height: 280px;
    min-height: 250px;
  }

  .center-item {
    margin: 0px 15vw;
  }
}
