.tech-stack-container {
  width: 100%;
  max-width: 100%;
  margin: 2vw 0;
  border-radius: 20px;
  border: 1px solid white;
  transition: all ease-in-out 200ms;
  h3 {
    padding: 0px 3vw;
  }
  nav {
    padding: 0px 3vw;
  }
  nav {
    ul {
      list-style: none;
      padding: 0;
      li {
        display: inline-block;
        padding: 0px 5px;
        p {
          padding: 0;
          margin: 0;
          cursor: pointer;
        }
      }
    }
  }
}

.tech-stack-container:hover {
  border: 1px solid #1e6fd9;
  background-color: #1e6fd9;
}
