#work-component {
  h2 {
    @media screen and (max-width: 899px) {
      font-size: 8vw;
    }
    @media screen and (min-width: 899px) {
      font-size: 4vw;
    }
    text-align: right;
    padding-right: 4vw;
    margin-top: 7vw;
    margin-bottom: 2vw;
  }

  .grid-work {
    transition: all ease-in-out 400ms;
    border-top: 1px solid white;
    .grid-sub-work {
      max-width: 1800px;
    }
    p {
      cursor: default;
      transition: all ease-in-out 400ms;
    }
  }
  .grid-work:last-child {
    border-bottom: 1px solid white;
  }
  .grid-work:hover {
    border-top: 1px solid #1e6fd9;
    background-color: #1e6fd9;
  }
  .grid-work:last-child:hover {
    border-bottom: 1px solid #1e6fd9;
  }

  .grid-work-time {
    p {
      margin: 0;
    }
  }

  .grid-role {
    padding-right: 4vw !important;
  }
}
