.prev,
.next {
  z-index: 1;
  border: none;
  color: white;
  border-radius: 100%;
  border: 1px solid white;
  background-color: rgba($color: #000000, $alpha: 0.9);
}

.slider-control {
  @media screen and (max-width: 899px) {
    display: none !important;
  }

  .next,
  .prev {
    padding: 38px 40px;
  }

  .next {
    box-shadow: 66px -10px 33px 94px rgba(0, 0, 0, 0.8);
  }
  .prev {
    box-shadow: -66px -10px 33px 94px rgba(0, 0, 0, 0.8);
  }
}

.slider-control-mobile {
  @media screen and (min-width: 899px) {
    display: none !important;
  }

  display: flex;
  .next,
  .prev {
    padding: 17px 20px;
  }
}
