@import "../../assets/scss/variables";

@media screen and (min-width: 899px) {
  #landing-component .description-mobile {
    display: none;
  }
  #landing-contact-info {
    width: 50%;
  }
}

@media screen and (max-width: 899px) {
  #landing-component .description-mobile {
    display: block;
    margin: 10px 0px;
  }
  #landing-desc {
    display: none;
  }
  #landing-contact-info {
    width: 80%;
    margin-top: 20px;
    a {
      font-size: 11px;
    }
  }
}

#landing-component {
  width: 100%;
  color: #fff;
  padding: 50px 0px;

  h2.left-title {
    font-size: 5.5vw;
    margin: 0;
    padding: 0;
  }

  .to-project {
    display: inline-block;
    border: none;
    width: 70%;
    height: 40px;
    font-size: 16px;
    background-color: #fff;
    border-radius: 20px;
    margin-right: 5px;
    span {
      color: #000;
    }
  }
  .to {
    display: inline-block;
    border: none;
    width: 25%;
    height: 40px;
    font-size: 16px;
    background-color: #fff;
    border-radius: 20px;
  }

  .description,
  .description-mobile {
    font-weight: 100;
    p {
      margin: 0;
      padding: 0;
      b {
        font-weight: 900;
      }
    }
  }

  .description {
    font-size: 1.5vw;
  }

  .description-mobile {
    font-size: 3vw;
  }

  h2.right-title {
    font-size: 5.5vw;
    margin: 0;
    padding: 0;
  }

  a.contact-info-link {
    display: flex;
    max-width: 30vw;
    align-items: center;
    align-content: center;
    justify-content: center;
    color: #fff;
    text-decoration: none;
    border-radius: 20px;
    border: 1px solid #fff;
    padding-top: 10px;
    padding-bottom: 10px;
    img.contact-info-link-icon {
      margin-right: 8px;
    }
  }
}
